<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <button class="mui-btn mui-btn-blue mui-btn-link mui-btn-nav mui-pull-left" @click="goBack">
        <span class="mui-icon mui-icon-left-nav"></span>返回
      </button>
      <h1 class="mui-title">手机号码修改</h1>
    </header>
    <div class="mui-content" style="margin-top:3px;">
      <form class="mui-input-group">
        <div class="mui-input-row">
          <label>证件类型</label>
          <input
            type="text"
            class="mui-input-clear"
            placeholder="请选择证件类型"
            :value="userType"
            @click="showIdType"
            readonly
          >
        </div>
        <div class="mui-input-row">
          <label>证件号码</label>
          <input type="text" class="mui-input-clear" placeholder="请输入证件号码" v-model="userLoginCode">
        </div>
        <div class="mui-input-row">
          <label>新手机号码</label>
          <input
            type="text"
            id="tel"
            class="mui-input-clear"
            placeholder="请输入手机号"
            v-model="newUserPhones"
          >
        </div>
        <div class="mui-button-row">
          <button
            type="button"
            data-loading-icon="mui-spinner mui-spinner-custom"
            class="mui-btn mui-btn-primary" @click="UserLogin"
          >提交</button>
        </div>

        <div id="certf_type" class="mui-popover mui-popover-bottom mui-popover-action">
          <ul class="mui-table-view">
            <li class="mui-table-view-cell" v-for="item in this.$route.params.idTypeList">
              <a
                href="javascript:return false;"
                @click="selectLi(item.parameterValue,item.areaCode)" >{{item.parameterValue}}</a>
            </li>
          </ul>
          <ul class="mui-table-view">
            <li class="mui-table-view-cell">
              <a href="#certf_type">
                <b>取消</b>
              </a>
            </li>
          </ul>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "UpdataUserPhone",
  data() {
    return {
      userType: "居民身份证",
      userTypeCode: "120001",
      userLoginCode: "1234201011234",
      newUserPhones: "13693268839",
      validInfo: true
      // ,
      // selectData:this.$store.getters.user.idTypeList
    };
  },
  methods: {
    goBack: function() {
      window.history.go(-1);
    },
    showIdType: function() {
      this.mui("#certf_type").popover('toggle');
    },
    selectLi: function(idRem,idType) {
      this.userType = idRem;
      this.userTypeCode = idType;
      this.mui("#certf_type").popover('toggle');
    },
    UserLogin: function() {
      let userCode = this.$store.getters.user.userCode;
      if (userCode) {
        if (!this.userLoginCode) {
          alert("请输入证件号码");
          return;
        }
        if (!this.newUserPhones) {
          alert("请输入新手机号码");
          return;
        }
        console.log(
          "用户标识：" +
            userCode +
            " ;证件类型：" +
            this.userTypeCode +
            "_" +
            this.userType +
            " ;用户证件号码：" +
            this.userLoginCode +
            " ;用户新手机号：" +
            this.newUserPhones
        );
        this.$http
          .post("/updateUserPhone", {
            userCode: userCode,
            userTypeCode: this.userTypeCode,
            userLoginCode: this.userLoginCode,
            newUserPhones: this.newUserPhones,
            validInfo : this.validInfo
          })
          .then(
            response => {
              let type = response.data.status;
              if (type) {
                console.log(response.data.msg);
                if (type == "identifying") {
                  this.$store.commit("LocalUser", {
                    userCode: userCode,
                    userTypeCode: this.userTypeCode,
                    userLoginCode: this.userLoginCode,
                    userPhones: this.newUserPhones
                  });
                  this.$router.push("/identifying");
                } else {
                  alert(response.data.msg);
                }
              }
            },
            error => {
              console.log(error);
            }
          );
      } else {
        alert("数据异常，请重新登录");
      }
    }
  }
};
</script>
<style scoped>
</style>



